import React, { Component } from 'react';

class About extends Component{
  render(){
    return(
      <div className="container-about">
        <h1>Acerca de m y este blog</h1>
        <p>Mi nombre es Fernando Segura, soy programador freelance para web (principalmente backend) y mvil en plataformas android.</p>
        <p>Lo se, otro blog de un programador con su nombre en el dominio que se pudiera encontrar en otro blog de este tipo?.</p>
        <p>En particular este blog lo hice por dos razones:</p>
        <p><b>1.-</b> Casi todos los das programo algo, ya sea un ejercicio, o por alguna razn se me ocurre un emprendimiento (realmente son sistemas relativamente sencillos bajo un contexto altruista xD). </p>
        <p>En el proceso me encuentro con problemas tcnicos con libreras o APIs, y en ocasiones doy soluciones entre una mezcla de posts en stackoverflow, issues reportados ms no solucionados en Github, y una indagacin por la documentacin oficial. </p>
        <p>Doy con la solucin, la aplico, pero en ocasiones se vuelve repetitivo en otros proyectos, y lleg a olvidar un poco del porque la solucin.
        Voy a estar compartiendo en este blog en cuanto encuentre una problemtica de este tipo, para aquel que llegue a toparse con este sitio, o por buena prctica y tener una buena comprensin de la solucin.</p>
      <p><b>2.-</b> Prctica. En el punto anterior mencione que casi todos los das programo algo, en particular en la semana donde escribo esto (10 de julio-16 de julio del 2017) me encontr la idea de hacer un mini proyecto en 1 da. Este blog fue concebido para practicar un poco React+Redux, aunque fracase en mi intento de hacerlo en 1 da (tuve una semana algo movida y no pude dedicarme en mis horas libres para terminarlo).</p>

    <h2>Qu contenido compartir?.</h2>
    <p>En particular me gusta programar con Ruby (Rails), Javascript (React+Redux, JQuery, me interesa React Native pero no me he metido a investigar, me interesa la Realidad Aumentada), Android. Desde hace unos meses me interesa aplicar realmente TDD y BDD a mi flujo de trabajo, estars leyendo muy probablemente sobre esto en el blog.</p>
    <p>Soy fan de asistir a eventos de emprendimiento, en particular los Startup Weekend.</p>
    <p><b>Eres programador?</b> Bienvenido, sientete libre de si ves algo tcnico y deseas hacer una crtica de algo hacerla.</p>
    <p><b>Eres emprendedor?</b> Bienvenido, en particular me llama la atencin todo lo relacionado con el emprendimiento social/altruista, quizs encuentres alguna nota donde puedas comentar algo al respecto. De igual manera, si tienes una idea de proyecto, y buscas alguien que te apoye, contactame y platicamos, quizs podamos lograr algo juntos: fernandosegom@gmail.com</p>
    <p><b>No tienes idea de como llegaste aqui?</b>: Suele suceder en ocasiones, sintete libre de dejar cualquier comentario que gustes.</p>
    <p>Saludos, bienvenido a este blog :), ten un excelente da.</p>
    <p>Puedes saber un poco mas de mi desde mis otras redes sociales:</p>
    <p><a href="https://www.linkedin.com/in/fernando-segura-72139928/">Linkedin</a>.</p>
    <p><a href="https://twitter.com/fernandosegom">Twitter</a></p>

      </div>



    )
  }
}

export default About;



