import React, { Component } from 'react';

class Footer extends Component{
  render(){
    return(
      <footer className="principal row">
        <div className="col-md-12 copyright-info">
          Pgina hecha con <a href="https://facebook.github.io/react/">
          <img alt="" className="icon-tool" src="/img/reactjsicon.png"/></a> y
          <a href="http://redux.js.org"><img alt="" className="icon-tool" src="/img/reduxicon.png"/></a>. Hospedada en <a href="https://github.com/fernandosg/fernandosg.github.io"><img alt="" src="/img/githubicon.png" className="icon-tool" /></a>
        </div>
      </footer>
    )
  }
}

export default Footer;



